div.search_wrapper {
    width: fit-content;
    height: fit-content;
    margin: auto;
    position: relative;
    display: flex;
    flex-direction: column;
}

div.search_wrapper img.thumbnail {
    position: absolute;
    height: 5rem;
    width: 5rem;
    margin: auto;
    top: 0;
    bottom: 0;
    margin-left: 1rem;
    box-shadow: 0 0 2rem var(--color2);
    opacity: .85;
}

div.search_wrapper input.search {
    font-size: 2.5rem;
    padding: 1rem;
    padding-left: 6rem;
    margin: auto;
    text-align: left;
    background-color: var(--color2);
    border-radius: 1rem;
    border: 1rem ridge var(--color3);
    overflow: hidden;
    text-overflow: ellipsis;
    box-shadow: inset 0 0 1rem black;
    font-family: 'Ubuntu Mono', sans-serif;
    font-weight: 900;
    padding-right: 3rem;
    box-shadow: 0 0 1rem var(--color2);

}

div.search_wrapper button.input {
    position: absolute;
    right: 1rem;
    top: 0;
    bottom: 0;
    background-color: transparent;
    border: 1rem solid transparent;
    margin: auto;
}

div.search_wrapper ol.dropdown {
    position: absolute;
    height: fit-content;
    max-height: 50rem;
    overflow: auto;
    width: 100%;
    padding: 0;
    margin-top: 6.5rem;
    border: 1rem ridge var(--color2);
    box-sizing: border-box;
    z-index: 11;
    background-color: var(--color3);
}

div.search_wrapper ol.dropdown {
    max-height: 50rem;
    overflow: scroll;
    position: absolute;
}



div.search_wrapper li button {
    display: flex;
    width: 100%;
    text-align: left;
    font-size: 2.5rem;
    font-weight: 900;
    background-color: #9ca7b4;
    color: black;
    box-shadow: inset 0 0 2rem black;
    font-family: 'Ubuntu Mono', sans-serif;
    margin: 0;
    padding: 0;
}

div.search_wrapper p {
    width: 100%;
    text-align: left;
    font-size: 2.5rem;
    font-weight: 900;
    filter: contrast(2);
    background-color: #9ca7b4;
    color: black;
    box-shadow: inset 0 0 2rem black;
    font-family: 'Ubuntu Mono', sans-serif;
    margin: 0;
    padding: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

div.search_wrapper li>button.reverse {
    text-overflow: initial;
    direction: rtl;
}

div.search_wrapper li>button.reverse>p {
    width: 100%;
    text-align: right;
}


div.search_wrapper li button img.thumbnail {
    box-shadow: 0 0 1rem black;
    margin-right: 1rem;
    height: 5rem;
    width: 5rem;
    border-radius: 50%;
    vertical-align: middle;
    position: static;
}

div.search_filter_wrapper>div:first-child{
    display: flex;
    justify-content: right;
    width: 25%;
}

div.search_filter_wrapper>div:last-child {
    display: flex;
    justify-content: left;
    width: 25%;
}

div.search_filter_wrapper,
div.trade_search_wrapper{
    display: flex;
    position: relative;
    justify-content: center;
    width: 50%;
    margin: auto;
}